.game-container {
  position: relative;
  width: 800px;
  height: 400px;
  margin: 20px auto;
  background-color: white;
  overflow: hidden;
}

.game-area {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
}

.character {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 20px;
  left: 50px;
  transition: bottom 0.5s;
}

.obstacle {
  width: 40px;
  height: 60px;
  background-color: #333;
  position: absolute;
  bottom: 20px;
}

.ground {
  width: 100%;
  height: 20px;
  background-color: #333;
  position: absolute;
  bottom: 0;
}

.score {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  font-weight: bold;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 32px;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 10px;
}

.game-over button {
  display: block;
  margin: 20px auto 0;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
}
